* {
	font-family: 'Lora', serif;
	font-size: 30px; }
body {
	a {
		text-decoration: none;
		color: black;
		&:visited {
			color: black; } }
	p {
		margin: 0 0 10px 0;
		span {
			color: orange; } }
	.nextPage,.prevPage {
		display: flex;
		flex-direction: row;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			color: #009AD0;
			img {
				height: 15px;
				width: auto; } } }
	h1 {
		font-size: 65px;
		color: red;
		margin-top: 25px;
		span {
			font-size: 65px;
			color: orange; } }
	h2 {
		font-size: 40px;
		span {
			font-size: 40px;
			color: orange; } }
	h3 {
		color: orange; }
	h5 {
		color: blue;
		font-size: 30px; }
	.card {
		border: none;
		img {
			margin: 10px auto 0;
			width: 200px;
			height: 200px; } }
	.row {
		margin: 50px 0; } }
header {
	margin-top: 50px;
	a {
		img {
			max-width: 100%; } }
	.language-selector {
		font-size: 25px;
		line-height: 1.5em;
		p {
			color: #009AD0; } } }
main {
	.mainImg {
		display: flex;
		justify-content: center;
		width: 100%;
		position: relative; }
	.mainImgMobile {
		display: none; }
	.bigMainImg {
		display: flex;
		justify-content: center; }
	.nextFlex {
		display: flex;
		flex-direction: column;
		h1 {
			margin: 0 auto; }
		p {
			margin-bottom: 10px; }
		blockquote {
			margin-bottom: 10px; } }
	.price {
		.bitcoin {
			span {
				color: #BD8913;
				font-size: 30px; }
			.bitcontainer {
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				img {
					max-height: 200px; } } } }
	.signUp {
		display: flex;
		justify-content: center;
		// margin-top: 20px
		// width: 100%
		img {
			max-width: 100%; }
		h1 {
			// padding: 0 30px 0 30px
			white-space: nowrap; } }
	.contacts {
		display: flex;
		flex-direction: column;
		align-items: center;
		.imgContainer {
			display: flex;
			justify-content: center;
			img {
				height: 50px;
				width: auto; } } } }
.footerLine {
	grid-area: footer-line;
	background: url("../img/footer_Line.png"); }
footer {
	grid-area: footer;
	background: #f8f8f8;
	display: grid;
	grid-template-columns: 1fr;
	.footerContainer {
		display: flex;
		height: 70px;
		justify-self: center;
		align-items: center;
		p {
			font-size: 15px; } } }
@import  'media';
